import React from 'react';
import preloader from '../../../../img/Curve-Loading.gif';
import s from './ProfileInfo.module.css';
import Preloader from "../../../Common/Preloader";
import ProfileStatus from "./ProfileStatus";
import userPhoto from "../../../../img/1.png";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import {Button, Input} from "antd";
import {UploadOutlined} from '@ant-design/icons';

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <div>
            <Preloader/>
        </div>
    }
    const isEditMode = () => {
        if (props.editMode) {
            return props.deactivateProfileEditMode;
        }
        return props.activateProfileEditMode;
        // { props.editMode ? props.deactivateProfileEditMode : props.activateProfileEditMode}
    }
    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }
    return (
        <div>
            {props.isOwner &&
            <div className={s.editProfile}>
                <Button onClick={isEditMode()}> Edit</Button>
            </div>
            }
            <div>
                <img
                    src={props.profile.photos.large || userPhoto}
                    className={s.largeUserPhoto}/>
                <ProfileStatusWithHooks id={props.id} status={props.status} updateStatus={props.updateStatus}
                                        isOwner={props.isOwner}/>

                {props.isOwner && props.editMode ?
                    <div className={s.photoUpload}><Input icon={<UploadOutlined/>} type={"file"}
                                                          onChange={onMainPhotoSelected}/></div> : null
                }

            </div>
            <div className={s.discriptionBlock}>

            </div>
        </div>
    );
}

export default ProfileInfo;